<template>
  <div class="mb-2">
    <el-table 
      :data="contacts" 
      border 
      size="mini">
      <el-table-column type="index" width="35" align="center" />
      <el-table-column 
        v-for="(c, i) in columnDefs"
        :key="i"
        :label="c.label"
        :width="c.width"
        :prop="c.prop"
        :sortable="c.sortable">
      </el-table-column>
      <el-table-column width="100" fixed="right" label="Operations">
        <template slot-scope="scope">
          <el-button 
            type="text" 
            class="info--text" 
            size="small"
            @click.native.prevent="remove(scope.$index)">
            Remove
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-button class="mt-2" type="primary" size="mini" plain>
      <i class="icon icon-plus el-icon--left" /> Add new contact
    </el-button>
  </div>
</template>

<script>
export default {
  data: () => ({
    contacts: [],
    columnDefs: [
      { label: 'Name', prop: 'name', sortable: true },
      { label: 'Salutation', prop: 'salutation', sortable: true },
      { label: 'Email', prop: 'email', sortable: true },
      { label: 'Mobile number', prop: 'mobile_number', sortable: true },
      { label: 'Office number', prop: 'office_number', sortable: true },
      { label: 'Fax number', prop: 'fax_number', sortable: true }
    ]
  })
}
</script>



