<template>
  <div>
    <el-input
      v-model="filterText"
      placeholder="Filter keyword"
      class="mb-1">
    </el-input>
    <el-tree
      :data="data"
      :props="defaultProps"
      :filter-node-method="filterNode"
      :expand-on-click-node="false"
      highlight-current
      check-on-click-node
      class="filter-tree"
      node-key="id"
      ref="tree"
      default-expand-all
      @node-click="selectedEntity">
    </el-tree>
  </div>
</template>

<script>
export default {
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val)
    }
  },

  data() {
    return {
      filterText: '',
      data: [{
        id: 1,
        label: 'Level one 1',
        children: [{
          id: 4,
          label: 'Level two 1-1',
          children: [{
            id: 9,
            label: 'Level three 1-1-1'
          }, {
            id: 10,
            label: 'Level three 1-1-2'
          }]
        }]
      }, {
        id: 2,
        label: 'Level one 2',
        children: [{
          id: 5,
          label: 'Level two 2-1'
        }, {
          id: 6,
          label: 'Level two 2-2'
        }]
      }, {
        id: 3,
        label: 'Level one 3',
        children: [{
          id: 7,
          label: 'Level two 3-1'
        }, {
          id: 8,
          label: 'Level two 3-2'
        }]
      }],
      defaultProps: {
        children: 'children',
        label: 'label'
      }
    }
  },

  methods: {
    filterNode(value, data) {
      if (!value) return true
      return data.label.indexOf(value) !== -1
    },

    selectedEntity(relationship) {
      this.$refs.tree.setCheckedKeys([])
      this.$refs.tree.setCheckedNodes([relationship])
      this.$store.commit('RMC/UPDATE_SELECTED_RELATIONSHIP', relationship)
    }
  }
}
</script>



