<template>
  <el-row :gutter="30">
    <el-form 
      label-position="left" 
      label-width="170px"
      size="mini"> 
      <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8">
        <el-form-item label="Business Portfolio">
          <el-input v-model="form.business_portfolio" />
        </el-form-item>
      </el-col>

      <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8">
        <el-form-item label="Credit Portfolio">
          <el-input v-model="form.credit_portfolio" />
        </el-form-item>
      </el-col>
    </el-form>
  </el-row>
</template>

<script>
export default {
  data: () => ({
    form: {
      business_portfolio: null,
      credit_portfolio: null
    }
  })
}
</script>



