<template>
  <el-row :gutter="30">
    <el-form 
      label-position="left" 
      label-width="170px"
      size="mini"> 
      <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8">
        <el-form-item label="T24 system ID">
          <el-input v-model="T24_system_id" />
        </el-form-item>
        <el-form-item label="Long name">
          <el-input v-model="long_name" />
        </el-form-item>
        <el-form-item label="Alias">
          <el-input v-model="short_name" />
        </el-form-item>
      </el-col>

      <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8">
        <el-form-item label="Tax code">
          <el-input v-model="tax_code" />
        </el-form-item>
        <el-form-item label="Legal ID">
          <el-input v-model="legal_id" />
        </el-form-item>
        <el-form-item label="Registration number">
          <el-input v-model="registration_number" />
        </el-form-item>
      </el-col>

      <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8">
        <el-form-item label="Entity type">
          <span>{{ entity_type ? entity_type.value : '' }}</span>
        </el-form-item>
        <el-form-item label="Business type">
          <span>{{ business_type ? business_type.value : '' }}</span>
        </el-form-item>
      </el-col>
    </el-form>
  </el-row>
</template>

<script>
import { mapFields } from 'vuex-map-fields'

export default {
  computed: {
    ...mapFields('RMC', [
      '_entity.entity_type',
      '_entity.business_type',
      '_entity.T24_system_id',
      '_entity.long_name',
      '_entity.short_name',
      '_entity.tax_code',
      '_entity.legal_id',
      '_entity.registration_number'
    ])
  }
}
</script>



