<template>
  <div class="mt-4">
    <transition name="el-fade-in-linear">
      <div 
        v-if="!$store.state.RMC.selectedRelationship" 
        class="text-md-center text-sm-center text-xs-center title">
        Select a Entity
      </div>
      <div v-else>
        <!-- <i class="icon icon-tree" /> -->
        <span>Relationship for {{ $store.state.RMC.selectedRelationship.label }}</span>
        <el-divider direction="vertical"></el-divider>
        <el-link :underline="false" type="info" @click="$router.push('/rmc/bubble-view')">
          Bubble view
          <i class="el-icon-grape el-icon--right"></i> 
        </el-link>
        <el-divider direction="vertical"></el-divider>
        <el-link :underline="false" :href="'/rmc/hierachy'" type="info">
          Hierachy view
          <i class="icon icon-tree el-icon--right"></i> 
        </el-link>
        <el-divider direction="vertical"></el-divider>
        <el-link :underline="false" type="info" @click="$router.push('/rmc/zoomchart')">
          Netchart view
          <i class="icon icon-accessibility el-icon--right"></i> 
        </el-link>
        <el-table
          :data="tableData"
          class="mt-3"
          size="small"
          border>
          <el-table-column type="index" width="35" align="center" />
          <el-table-column 
            v-for="(c, i) in columnDefs"
            :key="i"
            :label="c.label"
            :width="c.width"
            :sortable="c.sortable">
            <template slot-scope="scope">
              {{ scope.row[c.prop] }}
            </template>
          </el-table-column>
          <el-table-column width="100" fixed="right" label="Operations">
            <template slot-scope="scope">
              <el-button 
                type="text" 
                class="info--text" 
                size="small"
                @click.native.prevent="remove(scope.$index)">
                Remove
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-button type="primary" size="mini" class="mt-2" plain>
          <i class="icon icon-plus el-icon--left" /> Add new relationship
        </el-button>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  data: () => ({
    tableData: [],

    columnDefs: [
      { label: 'Parent', prop: 'parent', sortable: true },
      { label: 'Child', prop: 'child', sortable: true },
      { label: 'Relationship Type', prop: 'relationship_type', sortable: true },
      { label: 'Aggregation (%)', prop: 'aggregation', sortable: true }
    ],
    relationship_types: []
  })
}
</script>



