<template>
  <div class="mb-2">
    <el-table 
      :data="address" 
      border 
      size="mini">
      <el-table-column type="index" width="35" align="center" />
      <el-table-column 
        v-for="(c, i) in columnDefs"
        :key="i"
        :label="c.label"
        :width="c.width"
        :prop="c.prop"
        :sortable="c.sortable">
      </el-table-column>
      <el-table-column width="100" fixed="right" label="Operations">
        <template slot-scope="scope">
          <el-button 
            type="text" 
            class="info--text" 
            size="small"
            @click.native.prevent="remove(scope.$index)">
            Remove
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dropdown 
      class="mt-2" 
      trigger="click" 
      placement="bottom-start"
      @command="selectedContactType">
      <el-button type="primary" size="mini" plain>
        <i class="icon icon-plus el-icon--left" /> Add new address
      </el-button>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item 
          v-for="c in address_types"
          :key="c.id"
          :command="c">
          {{ c.label }}
        </el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
  </div>
</template>

<script>
export default {
  data: () => ({
    address: [],
    columnDefs: [
      { label: 'Address type', prop: 'address_type', sortable: true },
      { label: 'Address', prop: 'address', sortable: true },
      { label: 'District', prop: 'district', sortable: true },
      { label: 'City', prop: 'city', sortable: true },
      { label: 'State/Province/Region', prop: 'state_province_region', sortable: true },
      { label: 'Country', prop: 'country', sortable: true },
      { label: 'Zip/Postal code', prop: 'zip_postal_code', sortable: true }
    ],
    address_types: [
      { label: 'Mailing', id: 'mailing' },
      { label: 'Registered', id: 'registered' },
      { label: 'Billing', id: 'billing' }
    ]
  }),

  methods: {
    selectedContactType(e) {
      console.log(e)
    }
  }
}
</script>



