<template>
  <el-row :gutter="30">
    <el-form 
      label-position="left" 
      label-width="170px"
      size="mini"> 
      <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8">
        <el-form-item label="Primary Banking Officer">
          <el-input v-model="form.primary_banking_officer" />
        </el-form-item>
      </el-col>

      <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8">
        <el-form-item label="Primary Credit Officer">
          <el-input v-model="form.primary_credit_officer" />
        </el-form-item>
      </el-col>
    </el-form>
  </el-row>
</template>

<script>
export default {
  data: () => ({
    form: {
      primary_banking_officer: null,
      primary_credit_officer: null
    }
  })
}
</script>



