<template>
  <el-tabs v-model="activeTabs" type="border-card">
    <el-tab-pane name="information">
      <span slot="label">
        Manage Information
      </span>
      <el-collapse v-model="activePanels">
        <el-collapse-item 
          v-for="(c, i) in informations"
          :key="i"
          :name="i">
          <template slot="title">
            <!-- <i :class="c.icon" /> -->
            <span class="primary--text">&nbsp;{{ c.title }}</span>
          </template>
          <component v-if="$store.state.RMC._entity !== {}" :is="c.component" class="mx-3" />
        </el-collapse-item>
      </el-collapse>
    </el-tab-pane>
    <el-tab-pane name="relationship">
      <span slot="label">
        Manage Relationship
      </span>
      <Toolbar class="mx-3" />
      <el-row>
        <el-col :span="8" :xs="24" :sm="24" :md="8" :lg="8" :xl="8">
          <Treeview class="ma-3" />
        </el-col>
        <el-col :span="16" :xs="24" :sm="24" :md="16" :lg="16" :xl="16">
          <Relationship class="ma-3" />
        </el-col>
      </el-row>
    </el-tab-pane>
  </el-tabs>
</template>

<script>
import EntityDetails from '@/components/RMC/Entity/ManageInformation/EntityDetails'
import Officer from '@/components/RMC/Entity/ManageInformation/Officer'
import ContactDetails from '@/components/RMC/Entity/ManageInformation/ContactDetails'
import Address from '@/components/RMC/Entity/ManageInformation/Address'
import Organization from '@/components/RMC/Entity/ManageInformation/Organization'
import Toolbar from '@/components/RMC/Entity/ManageRelationship/Toolbar'
import Treeview from '@/components/RMC/Entity/ManageRelationship/Treeview'
import Relationship from '@/components/RMC/Entity/ManageRelationship/Relationship'

export default {
  name: 'RMCEntiy',

  components: {
    Address,
    ContactDetails,
    EntityDetails,
    Officer,
    Organization,
    Toolbar,
    Treeview,
    Relationship
  },

  data: () => ({
    long_name: null,
    activePanels: [...Array(5).keys()],
    activeTabs: 'information',
    informations: [
      { title: 'Entity Details', component: 'EntityDetails', icon: 'el-icon-s-custom' },
      { title: 'Organization', component: 'Organization', icon: 'el-icon-s-custom' },
      { title: 'Officer', component: 'Officer', icon: 'el-icon-s-custom' },
      { title: 'Contact Details', component: 'ContactDetails', icon: 'el-icon-s-custom' },
      { title: 'Address', component: 'Address', icon: 'el-icon-s-custom' }
    ]
  }),

  created() {
    this.$store.dispatch('RMC/retrieveEntity', this.$route.params.entity_id)
      .then(entity => {
        // Change title
        this.long_name = entity.long_name 
        let title = entity.long_name 
        let brandName = this.$store.state.header.name
        let slogan = this.$store.state.header.slogan
        let separator = this.$store.state.header.separator
        document.title = [title, brandName, slogan].join(separator) 
      })
  }
}
</script>
<style lang="css" scoped>
>>>.el-tabs__content {
  padding-left: 0;
  padding-right: 0;
}
>>>.el-collapse-item__header,
>>>.el-collapse,
>>>.el-collapse-item__wrap {
  border: none;
}
>>>.el-collapse-item__content {
  padding-bottom: 0
}
>>>.el-collapse-item__header {
  font-size: 16px;
  padding-left: 15px;
  padding-right: 15px;
} 
>>>.el-select {
  width: 100%;
}
>>>.el-dialog__body {
  padding: 20px 30px;
}
</style>



